import { Component, forwardRef, Input, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { IOperation, OperationStatus } from '@c8y/client';
import {
  gettext,
  ListItemComponent,
  ProductExperienceEvent,
  ProductExperienceEventSource,
  PRODUCT_EXPERIENCE_EVENT_SOURCE
} from '@c8y/ngx-components';
import { BulkOperationListItemService } from '@c8y/ngx-components/operations/bulk-operation-list-item';
import {
  OperationsService,
  OperationStatusOptionsMap,
  OPERATION_STATUS_OPTIONS_MAP
} from '@c8y/ngx-components/operations/shared';
import { OperationsListService } from './operations-list.service';

@Component({
  selector: 'c8y-operations-list-item',
  templateUrl: './operations-list-item.component.html',
  providers: [
    {
      provide: PRODUCT_EXPERIENCE_EVENT_SOURCE,
      useExisting: forwardRef(() => OperationsListItemComponent)
    }
  ]
})
export class OperationsListItemComponent implements OnInit, ProductExperienceEventSource {
  @Input()
  operation: Partial<IOperation>;
  @Input()
  collapsed = true;
  @Input()
  readOnly = false;
  @Input()
  noExpandToggle = false;
  @Input()
  isSmartRulesMicroserviceAvailable: boolean;
  @ViewChild('listItem', { static: true }) listItem: ListItemComponent;
  refreshLoading = false;
  OPERATION_STATUS = OperationStatus;
  OPERATION_STATUS_OPTIONS_MAP: OperationStatusOptionsMap = OPERATION_STATUS_OPTIONS_MAP;
  deviceId: string | number =
    this.route.snapshot.parent?.data.contextData?.id ||
    this.route.snapshot.firstChild?.data.contextData?.id;
  inventoryPermissions: string[];
  smartRuleAdminPermissions: string[];
  operationTitle: string;
  productExperienceEvent: ProductExperienceEvent;

  constructor(
    public bulkOperationListItemService: BulkOperationListItemService,
    public sharedService: OperationsService,
    public operationsListService: OperationsListService,
    private route: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit() {
    this.operationTitle =
      this.operation.c8y_Command?.text ||
      this.operation.description ||
      gettext('No command text or description available');

    const smartRulesCreatePermissions = this.operationsListService.getSmartRulesCreatePermissions();
    if (smartRulesCreatePermissions) {
      this.inventoryPermissions = smartRulesCreatePermissions[0].anyRole;
      this.smartRuleAdminPermissions = smartRulesCreatePermissions[1].anyRole;
    }
  }

  createCustomizedBulkOperation($event?: Event) {
    if ($event) {
      $event.stopPropagation();
    }

    this.router.navigateByUrl(`/devicecontrol/single/create-bulk/${this.operation.id}`);
  }
}
