import { Component, forwardRef, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IOperation, IResultList } from '@c8y/client';
import {
  gettext,
  OperationRealtimeService,
  ProductExperienceEvent,
  ProductExperienceEventSource,
  PRODUCT_EXPERIENCE_EVENT_SOURCE
} from '@c8y/ngx-components';
import {
  SINGLE_OPERATION_EVENT_DEVICE_TAB,
  SINGLE_OPERATION_EVENT_OVERVIEW
} from '@c8y/ngx-components/operations/product-experience';
import {
  OperationStatusOptionsMap,
  OPERATION_STATUS_OPTIONS_MAP,
  StatusOption
} from '@c8y/ngx-components/operations/shared';
import { StatusFilterComponent } from '@c8y/ngx-components/operations/status-filter';
import { flatten } from 'lodash-es';
import { BehaviorSubject, combineLatest, Observable } from 'rxjs';
import { shareReplay, switchMap, tap } from 'rxjs/operators';
import { OperationsListService } from './operations-list.service';

@Component({
  selector: 'c8y-operations-list',
  templateUrl: 'operations-list.component.html',
  providers: [
    OperationRealtimeService,
    {
      provide: PRODUCT_EXPERIENCE_EVENT_SOURCE,
      useExisting: forwardRef(() => OperationsListComponent)
    }
  ]
})
export class OperationsListComponent implements ProductExperienceEventSource {
  deviceId: string | number = this.route.snapshot.parent.data.contextData?.id;
  OPERATION_STATUS_OPTIONS_MAP: OperationStatusOptionsMap = OPERATION_STATUS_OPTIONS_MAP;

  title: string;
  refreshLoading = false;
  isSmartRulesMicroserviceAvailable = false;
  statusFilter$: BehaviorSubject<StatusOption[]> = new BehaviorSubject(null);
  reload$: BehaviorSubject<void> = new BehaviorSubject(null);
  @ViewChild('statusFilter', { static: true }) statusFilter: StatusFilterComponent;

  operations$: Observable<IResultList<IOperation>> = combineLatest(
    this.statusFilter$,
    this.reload$
  ).pipe(
    tap(() => {
      this.refreshLoading = true;
    }),
    switchMap(([statusFilters]) => this.filter(statusFilters)),
    tap(() => {
      this.refreshLoading = false;
    }),
    shareReplay(1)
  );

  productExperienceEvent: ProductExperienceEvent = {
    eventName: !!this.deviceId ? SINGLE_OPERATION_EVENT_DEVICE_TAB : SINGLE_OPERATION_EVENT_OVERVIEW
  };

  constructor(
    public realtime: OperationRealtimeService,
    private operationsListService: OperationsListService,
    private route: ActivatedRoute
  ) {
    this.title = this.route.snapshot.parent.data.contextData
      ? this.route.snapshot.parent.data.contextData.name
      : gettext('Device control');
  }

  async ngOnInit() {
    this.isSmartRulesMicroserviceAvailable =
      await this.operationsListService.isSmartRulesMicroserviceAvailable();
  }

  filter(statusFilters: StatusOption[]) {
    const status =
      statusFilters && statusFilters.length > 0
        ? {
            status: flatten(statusFilters.map(statusFilter => statusFilter.status))
          }
        : {};

    return this.getOperations(status);
  }

  getOperations(filter?) {
    const queryFilter = this.deviceId ? { ...filter, deviceId: this.deviceId } : filter;
    return this.operationsListService.getOperations(queryFilter);
  }

  compareOperations(operationA: IOperation, operationB: IOperation): number {
    return (
      new Date(operationA.creationTime).getTime() - new Date(operationB.creationTime).getTime()
    );
  }
}
